// FileExplorer.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';

const FileExplorer = () => {
  const [files, setFiles] = useState([]);
  const [currentPath, setCurrentPath] = useState('/data');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchFiles = async () => {
      setLoading(true);
      try {
        const response = await axios.get(`/list?path=${currentPath}`);
        setFiles(response.data);
        setError(null);
      } catch (err) {
        setError('Failed to fetch files');
      } finally {
        setLoading(false);
      }
    };

    fetchFiles();
  }, [currentPath]);

  const handleNavigate = (path) => {
    setCurrentPath(path);
  };

  const handleDownload = async (fileName) => {
    try {
      const response = await axios.get(`/download?path=${currentPath}&name=${fileName}`, {
        responseType: 'blob',
      });
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', fileName);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    } catch (err) {
      setError('Failed to download file');
    }
  };

  const handleRename = async (oldName, newName) => {
    try {
      await axios.post('/rename', { oldName, newName, path: currentPath });
      setFiles(files.map(file => (file === oldName ? newName : file)));
    } catch (err) {
      setError('Failed to rename file');
    }
  };

  const handleDelete = async (fileName) => {
    try {
      await axios.delete(`/delete?path=${currentPath}&name=${fileName}`);
      setFiles(files.filter(file => file !== fileName));
    } catch (err) {
      setError('Failed to delete file');
    }
  };

  if (loading) return <p>Loading...</p>;
  if (error) return <p>{error}</p>;

  return (
    <div>
      <h2>File Explorer</h2>
      <div className="file-grid">
        {files.map((file, index) => (
          <div key={index} className="file-item">
            <p>{file}</p>
            <div className="file-actions">
              <button onClick={() => handleNavigate(`${currentPath}/${file}`)}>Open</button>
              <button onClick={() => handleDownload(file)}>Download</button>
              <button onClick={() => handleRename(file, prompt('Enter new name'))}>Rename</button>
              <button onClick={() => handleDelete(file)}>Delete</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default FileExplorer;
